header {
    position: fixed;
    z-index: 9999999999999999999;
    top: 0;
    left: 0;
    right: 0;
    height: 45px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    padding: 0 30px 0 30px;
    width: 100vw;
}


.header--logo {
    display: flex;
    height: 40px;
    font-size: 25px;
    font-weight: 700;
    align-items: center;
}


.header--logo a {
    text-decoration: none;
    color: black;
}


.header--logo img {
    width: 100%;
    height: 100%;
}


.header--user {
    display: flex;
    align-items: center;
    height: 40px;
    font-size: 25px;
    font-weight: 700;
    text-align: center;
}

.header--user a {
    text-decoration: none;
    color: black;
}

.header--user img {
    height: 100%;
    border-radius: 3px;
}

.header--navMenu {
    display: flex;
    flex-direction: row;
}


.header--live,
.header--series,
.header--movies,
.header--mylist {
    display: flex;
    align-items: center;
    height: 40px;
    font-size: 22px;
    font-weight: 700;
    margin-left: 40px;
    margin-right: 40px;
    color: black;
    cursor: pointer;
}

.header--dropmenu {
    display: none;
    align-items: center;
    height: 40px;
    font-size: 22px;
    font-weight: 700;
    margin-left: 40px;
    margin-right: 40px;
    color: black;
    cursor: pointer;
}


@media (max-width: 1080px) {
    .header--logo {
        font-size: 15px;
    }

    .header--live,
    .header--series,
    .header--movies,
    .header--mylist {
        display: none;
        font-size: 15px;
        margin-left: 20px;
        margin-right: 20px;
    }

    .header--dropmenu {
        display: flex;
        font-size: 16px;
        margin-left: 20px;
        margin-right: 20px;
    }

    .header--user {
        font-size: 15px;
    }
}


@media (max-width: 760px) {
    .header--logo {
        font-size: 15px;
    }


    .header--navMenu {
        justify-content: center;
    }
    

    .header--live,
    .header--series,
    .header--movies,
    .header--mylist {
        display: none;
        font-size: 15px;
        margin-left: 20px;
        margin-right: 20px;
    }

    .header--dropmenu {
        display: flex;
        font-size: 16px;
        margin-left: 20px;
        margin-right: 20px;
    }

    .header--user {
        font-size: 15px;
    }
}


